import React from 'react'


const Contact=()=>{

  
const formSubmit=()=>{
  alert(`Form has been successfully Submitted`)
};
  return(
    <div>
    <div className="my-5">
    <h1 className="text-center">Contact US</h1>  
    </div> 
    <div className="container div_container">
      <div className="row row d-flex justify-content-center">
        <div className="col-md-6 col-10 max-auto">
        <form onSubmit={formSubmit} action="">
        <div className="mb-3">
             <label for="exampleFormControlInput1" className="form-label">Full Name</label>
             <input type="text" className="form-control" id="exampleFormControlInput1" required placeholder="Enter Your Name"/>
        </div>

        <div className="mb-3">
          <label for="exampleFormControlInput1" className="form-label">Phone Number</label>
          <input type="text" className="form-control" id="exampleFormControlInput1" required placeholder="Mobile Number"/>
        </div>

        <div className="mb-3">
          <label for="exampleFormControlInput1" className="form-label">Email address</label>
          <input type="email" className="form-control"id="exampleFormControlInput1"           
          placeholder="abc@example.com"/>
        </div>

        <div className="mb-3">
          <label for="exampleFormControlTextarea1" className="form-label">Message</label>
          <textarea className="form-control" id="exampleFormControlTextarea1" rows="3">
          </textarea>
        </div>        

      <div class="col-12">
    <button class="btn btn-success" type="submit">Submit form</button>
    </div>
   </form>        
  </div>
  </div>
</div>
</div>
  )
}

export default Contact;